/* eslint-disable react/prop-types */
import React from "react";
import "./Contact.scss";
import CV from "../../assets/resume.pdf";

const Contact = props => {
  const { mode } = props;
  return (
    <div className={`contact_section-${mode}`}>
      <h1 className="contact_title">CONTACT &amp; CV</h1>
      <div className="contact_text">
        <ul>
          <li>
            <p className="contact_text-colored">LINKED IN:</p>
            mylinkredin.com
          </li>
          <li>
            <p className="contact_text-colored">EMAIL:</p> myemail@gmail.com{" "}
          </li>
          <li>
            {" "}
            <p className="contact_text-colored">FACEBOOK:</p>
            www.facebook.com/myfacebooks
          </li>
          <li>
            {" "}
            <p className="contact_text-colored">DISCORD:</p> #myDiscord
          </li>
          <li>
            {" "}
            <p className="contact_text-colored">CV:</p> You can download CV my
            by clicking&nbsp;
            <a href={CV} download className="download-link">
              here
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Contact;
