/* eslint-disable react/prop-types */
import React from "react";
import "./About.scss";

const About = props => {
  const { mode } = props;
  return (
    <div className={`about_section-${mode}`}>
      <h1 className="about_title">ABOUT ME</h1>
      <div className="about_text">
        &nbsp;I am a highly competent IT professional with a proven track record
        in designing websites, networking and managing databases. I have strong
        technical skills as well as excellent interpersonal skills, enabling me
        to interact with a wide range of clients. I am eager to be challenged in
        order to grow and further improve my IT skills. My greatest passion is
        in life is using my technical know-how to benefit other people and
        organisations.
      </div>
    </div>
  );
};

export default About;
