import React, { useState, useRef, useEffect } from "react";
import {
  FaFacebook,
  FaDiscord,
  FaLinkedinIn,
  FaMoon,
  FaSun,
  FaChevronUp
} from "react-icons/fa";
import Chat from "./components/Chat/Chat";
import Projects from "./components/Projects/Projects";
import About from "./components/About/About";
import Contact from "./components/Contact/Contact";
// import Hello from "./Hello";
import "./App.scss";
import "./Styles.scss";
import "./Spinner.scss";

function App() {
  const [mode, setMode] = useState("dark");

  const changeModeLight = () => {
    setMode("light");
  };
  const changeModeDark = () => {
    setMode("dark");
  };

  const aRef = useRef(null);
  const pRef = useRef(null);
  const cRef = useRef(null);
  const topRef = useRef(null);
  const refs = {
    aRef,
    pRef,
    cRef
  };
  const [scrollPosition, setScrollPosition] = useState(0);
  const [buttonVisible, setButtonVisible] = useState(false);
  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };
  useEffect(() => {
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  useEffect(() => {
    if (scrollPosition > 600) setButtonVisible(true);
    else setButtonVisible(false);
  }, [scrollPosition]);
  // scroll to functions
  const scrollToProjects = () => {
    //  const anchor = document.querySelector("#project");
    pRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
  };
  const scrollToAbout = () => {
    // const anchor = document.querySelector("#about");
    aRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
  };
  const scrollToContact = () => {
    // const anchor = document.querySelector("#contact");
    cRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
  };
  const scrollToTop = () => {
    // const anchor = document.querySelector("#contact");
    topRef.current.scrollIntoView({ behavior: "smooth", block: "start" });
  };
  return (
    <div className={`body-${mode}`}>
      <hr ref={topRef} />
      <Chat
        mode={mode}
        scrollP={scrollToProjects}
        scrollA={scrollToAbout}
        scrollC={scrollToContact}
        ref={refs}
      />
      <div className={`page_${mode}`}>
        <div className={`mode-select-${mode}`}>
          <FaMoon
            className={`icon-moon_${mode}`}
            onClick={() => changeModeDark()}
          />
          <FaSun
            className={`icon-sun_${mode}`}
            onClick={() => changeModeLight()}
          />
        </div>
        <div className={`icons-${mode}`}>
          <a
            href="https://www.linkedin.com/in/ivan-milosavljevic-08579b209/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FaLinkedinIn className={`icon_${mode}`} />
          </a>
          <a
            href="https://www.facebook.com/ivan.milosavljevic.336/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FaFacebook className={`icon_${mode}`} />
          </a>
          <a
            href="https://discord.gg/vHBQMG5D"
            target="_blank"
            rel="noopener noreferrer"
          >
            {" "}
            <FaDiscord className={`icon_${mode}`} />{" "}
          </a>
        </div>
      </div>
      <hr ref={aRef} />
      <About mode={mode} />
      <hr ref={pRef} />
      <Projects mode={mode} />
      <hr ref={cRef} />
      <Contact mode={mode} />
      <button
        type="button"
        className={`to_top_button-${buttonVisible}`}
        onClick={scrollToTop}
      >
        <FaChevronUp />
      </button>
    </div>
  );
}

export default App;
